module $ { export class $mol_stringer extends $mol_viewer {

	/// tagName \input
	tagName() {
		return "input"
	}

	/// enabled true
	enabled() {
		return true
	}

	/// hint \
	hint() {
		return ""
	}

	/// type \text
	type() {
		return "text"
	}

	/// attr * 
	/// 	placeholder < hint 
	/// 	type < type
	attr( key : any ) {
		switch( key ){
			case "placeholder" : return this.hint()
			case "type" : return this.type()
		}
		return <any>null
	}

	attr_keys(){
		return <string[]>["placeholder","type"]
	}

	/// disabled false
	disabled() {
		return false
	}

	/// value \
	@ $mol_prop()
	value( ...diff : any[] ) {
		return ( diff[0] !== void 0 ) ? diff[0] : ""
	}

	/// valueChanged > value
	valueChanged( ...diff : any[] ) {
		return this.value(  ...diff )
	}

	/// field * 
	/// 	disabled < disabled 
	/// 	value < valueChanged
	field( key : any ) {
		switch( key ){
			case "disabled" : return this.disabled()
			case "value" : return this.valueChanged()
		}
		return <any>null
	}

	field_keys(){
		return <string[]>["disabled","value"]
	}

	/// eventChange null
	@ $mol_prop()
	eventChange( ...diff : any[] ) {
		return ( diff[0] !== void 0 ) ? diff[0] : <any> null
	}

	/// event * input > eventChange
	event( key : any , ...diff : any[] ) {
		switch( key ){
			case "input" : return this.eventChange(  ...diff )
		}
		return <any>null
	}

	event_keys(){
		return <string[]>["input"]
	}

} }

