module $ { export class $mol_portioner_indicator extends $mol_viewer {

	/// widthStyle \0
	widthStyle() {
		return "0"
	}

	/// field * style.width < widthStyle
	field( key : any ) {
		switch( key ){
			case "style.width" : return this.widthStyle()
		}
		return <any>null
	}

	field_keys(){
		return <string[]>["style.width"]
	}

} }

module $ { export class $mol_portioner extends $mol_viewer {

	/// portion 0
	portion() {
		return 0
	}

	/// indWidthStyle \0
	indWidthStyle() {
		return "0"
	}

	/// indicator $mol_portioner_indicator widthStyle < indWidthStyle
	@ $mol_prop()
	indicator( ...diff : any[] ) {
		return ( diff[0] !== void 0 ) ? diff[0] : new $mol_portioner_indicator().setup( __ => { 
			__.widthStyle = () => this.indWidthStyle()
		} )
	}

	/// childs / < indicator
	childs() {
		return [].concat( this.indicator() )
	}

} }

