module $ { export class $mol_checker extends $mol_clicker {

	/// checked : false
	checked() {
		return ( <any> false )
	}

	/// attr * mol_checker_checked < checked
	attr( key : any ) {
		switch( key ){
			case "mol_checker_checked" : return this.checked()
		}
		return <any>null
	}

	attr_keys(){
		return <string[]>["mol_checker_checked"]
	}

	/// label \
	label() {
		return ""
	}

	/// childs / < label
	childs() {
		return [].concat( this.label() )
	}

} }

