module $ { export class $mol_svg extends $mol_viewer {

	/// tagName \svg
	tagName() {
		return "svg"
	}

	/// nameSpace \http://www.w3.org/2000/svg
	nameSpace() {
		return "http://www.w3.org/2000/svg"
	}

} }

module $ { export class $mol_svg_path extends $mol_svg {

	/// tagName \path
	tagName() {
		return "path"
	}

	/// geometry \
	geometry() {
		return ""
	}

	/// attr * d < geometry
	attr( key : any ) {
		switch( key ){
			case "d" : return this.geometry()
		}
		return <any>null
	}

	attr_keys(){
		return <string[]>["d"]
	}

} }

