module $ { export class $mol_linker extends $mol_viewer {

	/// tagName \a
	tagName() {
		return "a"
	}

	/// uri \
	uri() {
		return ""
	}

	/// current : false
	current() {
		return ( <any> false )
	}

	/// attr * 
	/// 	href < uri 
	/// 	mol_linker_current < current
	attr( key : any ) {
		switch( key ){
			case "href" : return this.uri()
			case "mol_linker_current" : return this.current()
		}
		return <any>null
	}

	attr_keys(){
		return <string[]>["href","mol_linker_current"]
	}

	/// patch : null
	patch() {
		return ( <any> null )
	}

} }

