module $ { export class $mol_switcher_demo extends $mol_switcher {

	/// color \
	@ $mol_prop()
	color( ...diff : any[] ) {
		return ( diff[0] !== void 0 ) ? diff[0] : ""
	}

	/// value > color
	value( ...diff : any[] ) {
		return this.color(  ...diff )
	}

	/// option * 
	/// 	red \Red
	/// 	green \Green
	/// 	blue \Blue
	option( key : any ) {
		switch( key ){
			case "red" : return "Red"
			case "green" : return "Green"
			case "blue" : return "Blue"
		}
		return <any>null
	}

	option_keys(){
		return <string[]>["red","green","blue"]
	}

} }

